"use client";

import React, { useEffect, useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Checkbox } from "@/components/ui/checkbox";
import apiClient from "@/lib/AxiosUtils";
import { Permission, User } from "@/interface/types";

const UserPermission = () => {
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [users, setUsers] = useState<User[]>([]);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [permissionsState, setPermissionsState] = useState<
    (Permission & { isCheckedNow: boolean })[]
  >([]);

  const getAllPermissions = async () => {
    try {
      const res = await apiClient.get(`/permissions/view`);
      const allPermissions: Permission[] = res.data.result || [];
      setPermissionsState(
        allPermissions.map((perm) => ({ ...perm, isCheckedNow: false }))
      );
    } catch (err) {
      console.error("Failed to fetch permissions", err);
    }
  };

  const getMergedUniquePermissions = (user: User): Permission[] => {
    const rolePermissions = user.userRoles.flatMap(
      (ur) => ur.roles.permissions
    );
    const combined = [...rolePermissions, ...user.userPermissions];
    const uniqueMap = new Map<string, Permission>();
    combined.forEach((perm) => {
      if (!uniqueMap.has(perm.permissionName)) {
        uniqueMap.set(perm.permissionName, perm);
      }
    });
    return Array.from(uniqueMap.values());
  };

  const searchUser = async (key: string) => {
    try {
      if (!key.trim()) {
        setUsers([]);
        return;
      }
      const res = await apiClient.get(`/user/search/${key}`);
      const foundUsers: User[] = res.data.result || [];
      setUsers(foundUsers);
    } catch (err) {
      console.error("Search error", err);
    }
  };

  const handleUserSelect = (user: User) => {
    setSelectedUser(user);
    const mergedPermissions = getMergedUniquePermissions(user);
    setPermissionsState((prev) =>
      prev.map((perm) => ({
        ...perm,
        isCheckedNow: mergedPermissions.some(
          (p) => p.permissionName === perm.permissionName
        ),
      }))
    );
  };

  const handleResetSelection = () => {
    setSelectedUser(null);
    setSearchTerm("");
    setUsers([]);
    getAllPermissions();
  };

  const handleCheckboxChange = (permId: number, checked: boolean) => {
    setPermissionsState((prev) =>
      prev.map((perm) =>
        perm.id === permId ? { ...perm, isCheckedNow: checked } : perm
      )
    );
  };

  useEffect(() => {
    getAllPermissions();
  }, []);

  useEffect(() => {
    const delay = setTimeout(() => {
      if (!selectedUser) searchUser(searchTerm);
    }, 300);
    return () => clearTimeout(delay);
  }, [searchTerm]);

  return (
    <div className="w-full flex flex-col items-center">
      <div className="w-full my-3">
        <p className="text-base font-bold mb-4">Manage User Permissions</p>
      </div>

      {!selectedUser && (
        <>
          <div className="mb-4 w-[70%]">
            <Input
              type="text"
              placeholder="Search users..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>

          {users.length > 0 && (
            <div className="w-[70%] bg-white border rounded p-4 shadow mb-4">
              <h3 className="text-sm font-medium mb-2">Select a user:</h3>
              <ul className="space-y-2">
                {users.map((user) => (
                  <li
                    key={user.userId}
                    className="p-2 rounded cursor-pointer hover:bg-gray-100 bg-secondary"
                    onClick={() => handleUserSelect(user)}
                  >
                    <p className="text-sm font-medium">{user.userEmail}</p>
                    <p className="text-xs text-gray-600">{user.mobileNo}</p>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </>
      )}

      {selectedUser && (
        <div className="w-[90%] border rounded p-4 bg-white shadow-md">
          <div className="flex justify-between items-center mb-4">
            <div>
              <h3 className="text-lg font-semibold">
                Permissions for{" "}
                <span className="text-primary">{selectedUser.userEmail}</span>
              </h3>
              <p className="text-sm text-gray-500">{selectedUser.mobileNo}</p>
            </div>
            <Button variant="outline" onClick={handleResetSelection}>
              Change User
            </Button>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-3 mt-3">
            {permissionsState.map((permission) => (
              <div
                key={permission.id}
                className="flex items-center space-x-2 border p-2 rounded-md"
              >
                <Checkbox
                  checked={permission.isCheckedNow}
                  onCheckedChange={(val) =>
                    handleCheckboxChange(permission.id, !!val)
                  }
                />
                <div className="text-sm">
                  <div className="font-medium">
                    {permission.permissionName}
                  </div>
                  <div className="text-muted-foreground">
                    {permission.description}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default UserPermission;
